<script>
	import { writable } from "svelte/store";
	let shifts = localStorage.getItem('shifts') === null ? new Array : JSON.parse(localStorage.getItem('shifts'));
	let selected = new Array;
	let newShift = {};
	let wage = localStorage.getItem('wage') === null ? 6.45 : localStorage.getItem('wage');
	let showForm = true;
	$: total = selected.length !== 0 ? sum(selected) : sum(shifts);

	function allChecked (){
		let all = document.getElementById('all');
		if (all.checked){
			selected = shifts;
		} else {
			selected = [];
		}
	}

	function addShift() {
		showForm = !showForm;
		newShift.diff = diff(newShift.start, newShift.end);
		newShift.wage = (wage*newShift.diff).toFixed(2);
		shifts = [...shifts, newShift];
		localStorage.setItem('shifts', JSON.stringify(shifts));
		newShift = {};
	}

	function del(){
		shifts = shifts.filter(shift => selected.includes(shift) === false);
		selected = [];
		localStorage.setItem('shifts', JSON.stringify(shifts));
	}

	function diff(start, end) {
		start = start.split(":");
		end = end.split(":");
		var startDate = new Date(0, 0, 0, start[0], start[1], 0);
		var endDate = new Date(0, 0, 0, end[0], end[1], 0);
		var diff = endDate.getTime() - startDate.getTime();
		var hours = diff / 1000 / 60 / 60;

		// If using time pickers with 24 hours format, add the below line get exact hours
		if (hours < 0)
		hours = hours + 24;

		return hours.toFixed(2);
	}

	function sum(arr){
		let arrSum = arr => arr.reduce((a,b) => a + b, 0);
		let a = arr.map(shift => parseFloat(shift.wage));
		return arrSum(a).toFixed(2);
	}

	function updateShift() {
		localStorage.setItem('wage', wage);
		let newShifts = shifts;
		newShifts.forEach(shift => {
			shift.wage = (wage*shift.diff).toFixed(2);
		})
		shifts = newShifts;
		localStorage.setItem('shifts', JSON.stringify(shifts));
	}
</script>

<svelte:head>
	<title>Shift Manager</title>
	<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/open-fonts@1.1.1/fonts/inter.min.css">
	<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/@exampledev/new.css@1.1.2/new.min.css">
</svelte:head>

<header>
	<h3>Shift Manager</h3>
</header>

<h4>Your shifts</h4>
<hr>

{#if shifts.length === 0}
	<p style="text-align: center; padding:1rem; color:gray;">No shifts yet</p>
{:else}
<form on:submit|preventDefault={updateShift}>
	<label for="wage">Your hourly wage : Â£</label>
	<input type="number" step="0.01" min="0" bind:value={wage} style="width: 80px">
	<button type="submit">Update</button>
</form>
<table>
	<thead>
		<th style="text-align: center;">
			<input id="all" type="checkbox" checked="{selected.length === shifts.length}" on:change={allChecked}>
		</th>
		<th>Date</th>
		<th>Start</th>
		<th>End</th>
		<th>Duration</th>
		<th>Wage</th>
	</thead>
	<tbody>
		{#each shifts as shift}
		<tr>
			<td style="text-align: center;">
				<input type="checkbox" bind:group={selected} value={shift} 
				checked="{selected.includes(shift)}">
			</td>
			<td>{shift.date}</td>
			<td>{shift.start}</td>
			<td>{shift.end}</td>
			<td>{shift.diff} hrs</td>
			<td>Â£ {shift.wage}</td>
		</tr>
		{/each}
	</tbody>
	<tfoot>
		<tr>
			<td colspan="6" id="total">Total : Â£ {total}</td>
		</tr>
	</tfoot>
</table>
<div style="float:right">
	<button on:click={() => {showForm = !showForm}}>New</button>
	<button on:click={del} disabled={selected.length === 0}>ðŸ—‘</button>
</div>

<br>
<div hidden={showForm}>
	<h4>Record A new shift</h4>
	<hr>
	<form on:submit|preventDefault={addShift}>
		<fieldset>
			<label for="date">ðŸ“… Date : </label>
			<input bind:value={newShift.date} id="date" type="date" required>
		</fieldset>
		<p style="font-size: small; color:gray">
			Please use 24 hour time format unless specified otherwise by your browser
		</p>
		<fieldset>
			<label for="start">ðŸ•’ Start Time : </label>
			<input bind:value={newShift.start} id="start" type="time" required><br>
			<label for="end">ðŸ•˜ End Time &nbsp;&nbsp;: </label>
			<input bind:value={newShift.end} id="end" type="time" required>
		</fieldset>
		<button type="submit" id="record">Record</button>
		<button type="reset">Reset</button>
	</form>
</div>

<footer>
	<hr>
	<p>Atharva Tidke &copy; 2020</p>
</footer>
{/if}

<style>
	table{
		overflow: scroll;
	}

	#total{
		text-align: right;
		padding-right: 1rem;
	}

	footer{
		color: gray;
	}
</style>